import { Link } from 'react-router-dom';

const Forum = ({forum}) =>{
    const {name, description} = forum
    return (
        <main className='forum'>
            <Link to={`/Forum/${name}`}>
                <h2> {name} </h2>
                <p> {description} </p>
            </Link>
            {/* <button>
                Join
                <Icon.PlusLg/>
            </button>
            <button>
                Delete
                <Icon.X/>
            </button> */}
        </main>
    )
}

export default Forum;